.diagnostic_menu__container {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  z-index: 10;
}

.diagnostic_menu__container>.diagnostic_menu__toggle_button {
  position: absolute;
  right: 0;
}

.diagnostic_menu__container--expanded {
  background: rgba(255, 255, 255, 0.9);
}

.diagnostic_menu__toggle_button {
  background: transparent;
  border: none;
  padding: 10px;
  cursor: pointer;
  outline: none;
}

.diagnostic_menu__toggle_button::after {
  font-family: monospace;
  content: '[+]';
  color: white;
  display: block;
}

.diagnostic_menu__toggle_button--expanded::after {
  content: '[-]';
  color: black;
}

.peer_state__container {
  width: 500px;
  padding: 10px;
}

.peer_state__header {
  display: -webkit-flex;
  display: flex;
}

.peer_state__header .diagnostic_menu__toggle_button {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 0 0 30px;
          flex: 0 0 30px;
  margin: 0;
  padding: 0;
  line-height: 10px;
}

.peer_state__header .diagnostic_menu__toggle_button::after {
  color: black;
}

.peer_state__title {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0;
  padding: 0;
}

.swarm_state__container {
  width: 500px;
  padding: 10px;
}

.diagnostic_table__key_cell {
  width: 200px;
}

.swarm-player-video {
  height: 100vh;
  width: 100vw;
}

body {
  background: #000;
  overflow: hidden;
}

.diagnostic-menu--indent-1 {
  right: 550px;
}

.diagnostic-menu--indent-2 {
  right: 1100px;
}

.diagnostic-menu--indent-3 {
  right: 1650px;
}

.graph-buttons {
  position: absolute;
  z-index: 1;
}

.join-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: row;
          flex-direction: row;
  left: 50%;
  margin-left: -250px;
  position: absolute;
  top: 40%;
  width: 500px;
}

.join-form input,
.join-form button {
  display: -webkit-flex;
  display: flex;
  font-size: 1.25em;
  padding: 10px;
  border: none;
}

.join-form input {
  -webkit-flex: 100% 1;
          flex: 100% 1;
}

.join-form button {
  -webkit-flex: 100px 1;
          flex: 100px 1;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-align-items:center;
          align-items:center;
  background-color: #ddd;
}

.idle {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.scanner {
  height: 20vh;
  min-width: 100vw;
  background: -webkit-linear-gradient(
    top,
    #000,
    #090909 10vh,
    #000 20vh
  );
  background: linear-gradient(
    180deg,
    #000,
    #090909 10vh,
    #000 20vh
  );
  -webkit-animation: idle-scroll-up 20s infinite;
          animation: idle-scroll-up 20s infinite;
}

.noise {
  margin-top: -350px;
  margin-left: -350px;
  height: calc(100vh + 700px);
  width: calc(100vw + 700px);
  background-image: url("/noise.png");
  -webkit-animation: idle-jitter 500ms infinite;
          animation: idle-jitter 500ms infinite
}

@-webkit-keyframes idle-scroll-up {
  0% {
    -webkit-transform: translate3d(0, 100vh, 0);
            transform: translate3d(0, 100vh, 0);
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
  }
  100% {
    -webkit-transform: translate3d(0, -20vh, 0);
            transform: translate3d(0, -20vh, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
}

@keyframes idle-scroll-up {
  0% {
    -webkit-transform: translate3d(0, 100vh, 0);
            transform: translate3d(0, 100vh, 0);
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
  }
  100% {
    -webkit-transform: translate3d(0, -20vh, 0);
            transform: translate3d(0, -20vh, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
}

@-webkit-keyframes idle-jitter {
  0% {
    -webkit-transform: translate3d(-95px, -105px, 0);
            transform: translate3d(-95px, -105px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  20% {
    -webkit-transform: translate3d(-18px, -2px, 0);
            transform: translate3d(-18px, -2px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  40% {
    -webkit-transform: translate3d(-166px, -127px, 0);
            transform: translate3d(-166px, -127px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  60% {
    -webkit-transform: translate3d(-77px, -90px, 0);
            transform: translate3d(-77px, -90px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  80% {
    -webkit-transform: translate3d(-134px, -54px, 0);
            transform: translate3d(-134px, -54px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  100% {
    -webkit-transform: translate3d(-35px, -114px, 0);
            transform: translate3d(-35px, -114px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
}

@keyframes idle-jitter {
  0% {
    -webkit-transform: translate3d(-95px, -105px, 0);
            transform: translate3d(-95px, -105px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  20% {
    -webkit-transform: translate3d(-18px, -2px, 0);
            transform: translate3d(-18px, -2px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  40% {
    -webkit-transform: translate3d(-166px, -127px, 0);
            transform: translate3d(-166px, -127px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  60% {
    -webkit-transform: translate3d(-77px, -90px, 0);
            transform: translate3d(-77px, -90px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  80% {
    -webkit-transform: translate3d(-134px, -54px, 0);
            transform: translate3d(-134px, -54px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
  100% {
    -webkit-transform: translate3d(-35px, -114px, 0);
            transform: translate3d(-35px, -114px, 0);
    -webkit-animation-timing-function: step-end;
            animation-timing-function: step-end;
  }
}

